        margin-top: 10px;
        margin-bottom: 20px;
    }
}

.author__urls-wrapper {
    position: relative;
    display: table-cell;
    vertical-align: middle;
    font-family: -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto",
        "Segoe UI", "Helvetica Neue", "Lucida Grande", Arial, sans-serif;
    z-index: 10;
    position: relative;
    cursor: pointer;
}

.author__urls-wrapper li:last-child a {
    margin-bottom: 0;
}

@media (min-width: 57.8125em) {
    .author__urls-wrapper {
        display: block;
    }
}

.author__urls-wrapper button {
    margin-bottom: 0;
}

@media (min-width: 57.8125em) {
    .author__urls-wrapper button {
        display: none;
    }
}

.author__urls {
    display: none;
    position: absolute;
    right: 0;
    margin-top: 15px;
    padding: 10px;
    list-style-type: none;
    border: 1px solid #f2f3f3;
    border-radius: 4px;
    background: #fff;
    z-index: -1;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    cursor: default;
}

@media (min-width: 57.8125em) {
    .author__urls {
        display: block;
        position: relative;
        margin: 0;
        padding: 0;
        border: 0;
        background: transparent;
        box-shadow: none;
    }
}

.author__urls:before {
    display: block;
    content: "";
    position: absolute;
    top: -11px;
    left: calc(50% - 10px);
    width: 0;
    border-style: solid;
    border-width: 0 10px 10px;
    border-color: #f2f3f3 transparent;
    z-index: 0;
}

@media (min-width: 57.8125em) {
    .author__urls:before {
        display: none;
    }
}

.author__urls:after {
    display: block;
    content: "";
    position: absolute;
    top: -10px;
    left: calc(50% - 10px);
    width: 0;
    border-style: solid;
    border-width: 0 10px 10px;
    border-color: #fff transparent;
    z-index: 1;
}

@media (min-width: 57.8125em) {
    .author__urls:after {
        display: none;
    }
}

.author__urls li {
    white-space: nowrap;
}

.author__urls a {
    display: block;
    margin-bottom: 5px;
    padding-right: 5px;
    padding-top: 2px;
    padding-bottom: 2px;
    color: inherit;
    font-size: 1em;
    text-decoration: none;
}

.author__urls a:hover {
    text-decoration: underline;
}

.btn,
#goog-wm-sb {
    display: inline-block;
    margin-bottom: 0.25em;
    padding: 0.5em 1em;
    color: #fff !important;
    font-family: -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto",
        "Segoe UI", "Helvetica Neue", "Lucida Grande", Arial, sans-serif;
    font-size: 0.75em;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    background-color: #7a8288;
    border: 0 !important;
    border-radius: 4px;
    cursor: pointer;
}

